import './css/style.css';
import newsGallery from './js/apiService';
import hitsListTemplate from './template/gallery-list.hbs';
import PNotify from '../node_modules/pnotify/dist/es/PNotify';

const refs = {
  searchForm: document.querySelector('#search-form'),
  galleryList: document.querySelector('#gallery'),
  loadMoreBtn: document.querySelector('button[data-action="Load more"]'),
};
refs.searchForm.addEventListener('submit', searchFormHandler);
refs.loadMoreBtn.addEventListener('click', loadButtonHandler);
function searchFormHandler(e) {
  e.preventDefault();
  const inputValue = e.currentTarget.elements.query.value;
  clearListItems();
  newsGallery.resetPage();
  newsGallery.searchQuery = inputValue;
  newsGallery.fetchHits().then(hits => {
    const markup = buildListGallery(hits);
    insertListItems(markup);
  });
}
function loadButtonHandler() {
  newsGallery.fetchHits().then(hits => {
    const markup = buildListGallery(hits);
    insertListItems(markup);
    window.scrollTo({
      top: window.scrollY + document.documentElement.clientHeight - 100,
      behavior: 'smooth',
    });
  });
}
function insertListItems(items) {
  refs.galleryList.insertAdjacentHTML('beforeend', items);
}
function buildListGallery(items) {
  return hitsListTemplate(items);
}
function clearListItems() {
  refs.galleryList.innerHTML = '';
}
