const baseUrl =
  'https://pixabay.com/api/?image_type=photo&orientation=horizontal';
export default {
  page: 1,
  key: '15841151-88c9abfa90c1bca6eb69caaff',
  per_page: 12,
  query: '',
  fetchHits() {
    const requestParams = `&q=${this.query}&page=${this.page}&per_page=${this.per_page}&key=${this.key}`;
    return fetch(baseUrl + requestParams)
      .then(response => response.json())
      .then(parsedResponse => {
        this.incrementPage();
        return parsedResponse.hits;
      })
      .catch(error => {
        console.log(error);
      });
  },
  get searchQuery() {
    return this.query;
  },
  set searchQuery(value) {
    this.query = value;
  },
  incrementPage() {
    this.page += 1;
  },
  resetPage() {
    this.page = 1;
  },
};
